<template>
    <div class="login">
     <!-- 头部导航栏 -->
       <van-nav-bar
         right-text="先瞧瞧？"
         border:false
         @click-right="goHome"
       >
       </van-nav-bar>


        <div class="icon">
          <img src="../assets/images/cf_icon.jpg" class="icon_bg auto-img" alt="">
          <div class="name"><span>咖趣</span></div>
        </div>

     <!-- 用户信息输入框 -->
      <div class="login_form">
          <van-form>
          <div class="username">    
            <van-field
            v-model="userInfo.phone"
              name="用户名"
              label="用户名"
              placeholder="用户名"
              autocomplete="off"
              color="#fff"
            
            />
          </div>
            <div class="pdw">
             <van-field
             v-model="userInfo.password"
              :type="showPassword ? 'password' : 'text'"
              name="密码"
              label="密码"
              placeholder="密码"
              :right-icon="showPassword ? 'closed-eye' : 'eye-o'"
              @click-right-icon = "showEye"
            
            />
            </div>

            <div class="pdw_forget">
                <span @click="forgotPsw('Forgot')">忘记密码?</span>
            </div>
           

            <!-- 登录注册按钮 -->
           <div class="btn">
            <div style="margin: 16px;">
              <van-button round block color=" #780d00" @click="login">
               登录
              </van-button>
            </div>
            <div style="margin: 16px;">
              <van-button round block type="default" @click="showregisterbox">
                注册
              </van-button>
            </div>
           </div>
             <!-- 登录注册按钮end -->
       </van-form>
        <!-- 用户信息输入框end -->


       <!-- 注册弹出框 -->
       <div class="register">
           <van-popup v-model="isShow" position="bottom" closeable class="register_item">
         <div class="register_title">
             注册
         </div>

         <van-form>
               <van-field
               v-model="registerInfo.phone"
                 name="手机号"
                 label="手机号"
                 placeholder="手机号"       
               />

                <van-field
                v-model="registerInfo.password"
                  :type="showPassword ? 'password' : 'text'"
                 name="密码"
                 label="密码"
                 placeholder="密码"
                 :right-icon="showPassword ? 'closed-eye' : 'eye-o'"
                 @click-right-icon = "showEye"
             
               />

               <van-field
               v-model="registerInfo.nickName"
                 type="text"
                 name="昵称"
                 label="昵称"
                 placeholder="昵称"
             
               />
              
               <div style="margin: 16px;">
                 <van-button round block type="info" @click="register">
                   注册
                 </van-button>
               </div>
         </van-form>

       </van-popup>
       </div>
      </div>

    </div>
</template>

<script>

 import '../assets/less/login.less'

//  导入表单验证模块
    import {validForm} from '../assets/js/validForm'

    // JS文件的无名方法导入方式
    // import validForm from '../assets/js/validForm'

    export default {
        name:'Login',
        data(){
            return{
                // 用户信息
                userInfo:{
                    phone:'',
                    password:'',

                },
                // 用户注册信息
                registerInfo:{
                    phone:'',
                    password:'',
                    nickName:'',

                },
                // 切换注册框
                isShow:false,
                // 切换密码显示状态
                showPassword:true
            }
        },
        methods: {
            
            showregisterbox(){
                this.isShow = !this.isShow;
            },
            showEye(){
            this.showPassword = !this.showPassword
            },

            // 账户注册
            register(){
            // 构建表单验证信息
            let o ={
                phone:{
                    value:this.registerInfo.phone,
                    errorMsg:'手机号格式不正确',
                    reg:/^1[3-9]\d{9}$/
                },
                password:{
                    value:this.registerInfo.password,
                    errorMsg:'密码格式不正确',
                    reg:/^[A-Za-z]\w{5,15}$/
                },
                nickName:{
                    value:this.registerInfo.nickName,
                    errorMsg:'昵称格式不正确',
                    reg:/^[\w\u4e00-\u9fa5]{1,10}$/
                }
            }

           let isPass =  validForm.valid(o);

           if (isPass) {

          console.log('this.appkey ==> ', this.appkey);

          //赋值用户注册信息
          let userInfo = Object.assign({}, this.registerInfo);
          userInfo.appkey = this.appkey;

          this.$toast.loading({
            message: '加载中...',
            forbidClick: true,
            duration:0
          });

          //发起注册请求
          this.axios({
            //请求类型
            method: 'POST',
            //请求路径
            url: '/register',

            //POST请求参数, object
            data: userInfo

          }).then(result => {
            this.$toast.clear();
          
          // 注册成功
            if(result.data.code == 100){
              this.isShow = false;
            }else{
              this.$toast(result.data.msg)
            }
          }).catch(err => {
            this.$toast.clear();
            console.log('err ==> ', err);
          })

        }

            },
            // 账户登录
            login(){

              let o = {

                phone:{
                    value:this.userInfo.phone,
                    errorMsg:'用户名格式不正确',
                    reg:/^1[3-9]\d{9}$/
                },
                password:{
                    value:this.userInfo.password,
                    errorMsg:'密码格式不正确',
                    reg:/^[A-Za-z]\w{5,15}$/
                },
              };

              let isPass = validForm.valid(o);

              if(isPass){

                let uInfo = Object.assign({}, this.userInfo);
                uInfo.appkey = this.appkey;

                this.$toast.loading({
                 message: '加载中...',
                 forbidClick: true,
                 duration:0
                })

                this.axios({

                  method:'POST',
                  url:'/login',
                  data: uInfo
                }).then(result => {

                 if(result.data.code == 200){
                   this.$toast.clear();
                  //  保存token以便后续验证登录
                  localStorage.setItem('tokenString',result.data.token);
                  this.$router.push({name:"Home"})
                 }else{
                   this.$toast(result.data.msg);
                 }
              
                }).catch(err => {
                 this.$toast.clear();
                 console.log('err ==> ', err);
               
                })
                
              }

            },
            //跳转页面
            goHome(){
              this.$router.push({name:'Home'})
            },
            // 忘记密码
              forgotPsw(name) {
                this.$router.push({name});
              }
        }
    }
</script>

